<template>
  <div class="section">
      <el-date-picker
        v-model="date"
        type="date"
        format="yyyy 年 MM 月 dd 日"
        value-format="yyyy-MM-dd"
        @change="getData"
        placeholder="选择日期">
        </el-date-picker>
       <table > 
          <tr>
              <td rowspan="3" width='10%'>基础信息</td>
              <td>井号</td>
              <td>{{daily_drilling_baseinfo.well_no}}</td>
              <td>施工队</td>
              <td >{{daily_drilling_baseinfo.operate_team}}</td>
              <td>当日进尺 (m)</td>
              <td>{{daily_drilling_baseinfo.class_footage}}</td>
              <td>累计进尺 (m)</td>
              <td>{{daily_drilling_baseinfo.acc_footage}}</td>
              <td>机械钻速（m/h）</td>
              <td>{{daily_drilling_baseinfo.rop}}</td>
              <td>日期</td>
              <td>{{daily_drilling_baseinfo.daily_at}}</td>
          </tr>
          <tr >
              <td>设计井深（m）</td>
              <td>{{daily_drilling_baseinfo.design_depth}}</td>
              <td>设计完钻地层（m）</td>
              <td>{{daily_drilling_baseinfo.design_layer}}</td>
              <td>当前井深（m）</td>
              <td>{{daily_drilling_baseinfo.current_depth}}</td>
              <td>当前地层</td>
              <td>{{daily_drilling_baseinfo.current_layer}}</td>
              <td>开钻时间</td>
              <td>{{daily_drilling_baseinfo.start_at}}</td>
              <td>开钻天数</td>
              <td>{{daily_drilling_baseinfo.drill_days}}</td>
          </tr>
          <tr>
              <td>井径(mm)</td>
              <td>{{daily_drilling_baseinfo.current_diameter}}</td>
              <td>上层套管下深（m）</td>
              <td>{{daily_drilling_baseinfo.case_depth}}</td>
              <td>上次封井器测试时间</td>
              <td>{{daily_drilling_baseinfo.last_test_at}}</td>
              <td>FIT/LOT试验当量比重(g/cm3)</td>
              <td>{{daily_drilling_baseinfo.experiment_density}}</td>
              <td>FIT/LOT 试验井深 (m)</td>
              <td>{{daily_drilling_baseinfo.experiment_depth}}</td>
              <td>平台经理</td>
              <td>{{daily_drilling_baseinfo.manager}}</td>
          </tr>
          <tr>
              <td rowspan="2">钻具组合</td>
              <td colspan="2">入井时间</td>
              <td>开次</td>
              <td colspan="9">钻具结构明细</td>
          </tr>
          <tr v-for="item in daily_bha_list" :key="item.start_at">
              <td colspan="2">{{item.start_at}}</td>
              <td>{{item.drilling_inorder}}</td>
              <td colspan="9">{{item.detail}}</td>
          </tr>
          <tr>
              <td rowspan="2">钻头</td>
              <td>钻头序号</td>
              <td>入井编号</td>
              <td>尺寸(mm)</td>
              <td>钻头厂家</td>
              <td colspan="2">钻头型号</td>
              <td>下入井深（m）</td>
              <td>排量（L/s）</td>
              <td>泵压(MPa)</td>
              <td>钻压(kN)</td>
              <td>转速(rpm)</td>
              <td>喷嘴组合(mm)</td>
          </tr>
          <tr v-for="item in daily_drilling_bit_list" :key="item.inwell_no">
              <td>&nbsp;</td>
              <td>{{item.inwell_no}}</td>
              <td>{{item.bit_size}}</td>
              <td>{{item.producer}}</td>
              <td colspan="2">{{item.bit_model}}</td>
              <td>{{item.start_depth}}</td>
              <td>{{item.flow}}</td>
              <td>{{item.rop}}</td>
              <td>{{item.wob}}</td>
              <td>{{item.rpm}}</td>
              <td>{{item.jet_diameter}}</td>
          </tr>
          <tr>
              <td rowspan="2">钻井液</td>
              <td>钻井液密度</td>
              <td>粘度</td>
              <td>含砂量</td>
              <td>泥饼厚度</td>
              <td>PH</td>
              <td>API失水</td>
              <td>HTHP失水</td>
              <td>静切力</td>
              <td>动切力</td>
              <td>n值</td>
              <td>k值</td>
              <td>摩阻系数</td>
          </tr>
          <tr>
              <td>{{drilling_fluid_data.density}}</td>
              <td>{{drilling_fluid_data.viscosity}}</td>
              <td>{{drilling_fluid_data.sand_ratio}}</td>
              <td>{{drilling_fluid_data.cake_thickness}}</td>
              <td>{{drilling_fluid_data.ph}}</td>
              <td>{{drilling_fluid_data.api_fluid_loss}}</td>
              <td>{{drilling_fluid_data.ht_fluid_loss}}</td>
              <td>{{drilling_fluid_data.gel_strength_s}}</td>
              <td>{{drilling_fluid_data.yield_value}}</td>
              <td>{{drilling_fluid_data.n_value}}</td>
              <td>{{drilling_fluid_data.k_value}}</td>
              <td>{{drilling_fluid_data.friction_factor}}</td>
          </tr>
           <tr>
              <td rowspan="4">工程简况</td>
              <td>起始时间</td>
              <td>结束时间</td>
              <td>时间(合计)</td>
              <td>工况（代码）</td>
              <td>性质</td>
              <td colspan="7">工作内容</td>
          </tr>
          <tr v-for="(item,i) in daily_workreport_info.workreport_list" :key="i" v-if="daily_workreport_info.workreport_list">
              <td>{{item.start_at}}</td>
              <td>{{item.end_at}}</td>
              <td>{{item.time}}</td>
              <td>{{item.code}}</td>
              <td>{{item.property}}</td>
              <td colspan="7">{{item.description}}</td>
          </tr>
          <tr>
              <td colspan="4" rowspan="2"><div id="char" style="height: 200px;"></div></td>
              <td colspan="1">当日重要事项</td>
              <td colspan="7">{{daily_workreport_info.issue}}</td>
          </tr>
          <tr>
              <td colspan="1">存在问题</td>
              <td colspan="7">{{daily_workreport_info.problem}}</td>
          </tr>
          <tr>
              <td>井身结构</td>
              <td colspan="12">{{well_structure}}</td>
          </tr>
          <tr>
              <td>钻井液用量</td>
              <td colspan="12">{{drilling_fluid_usage}}</td>
          </tr>
          <tr>
              <td>应急材料储备</td>
              <td colspan="12">{{emergency_material}}</td>
          </tr>
      </table>
  </div>
</template>

<script>
import echarts from 'echarts'
import request from "@/utils/request";
export default {
  props: ["route"],
  data () {
     return {
        date: '2018-07-20',
        daily_bha_list: [],
        daily_drilling_baseinfo: {},
        daily_drilling_bit_list: [],
        daily_dynamic_tool_list: [],
        drilling_fluid_data: {},
        daily_workreport_info:{},
        well_structure:'',
        drilling_fluid_usage:'',
        emergency_material:''
      }
  },

  components: {},

  computed: {},

  mounted: function() {
    this.getData()
  },

  methods: {
    getData() {
        let data = this.route;
        request({
        url: "/report/daily",
        method: "get",
        params: {
            ...data,
            date:this.date
        }
        }).then(res => {
            for(let i in res){
                this[i] = res[i]
            }
            this.draw()
        });
    },
    draw() {
        let char = echarts.init(document.getElementById('char'));
        char.setOption({
            tooltip : {
                trigger: 'item',
                formatter: "{a} <br/>{b} : {c} ({d}%)"
            },
            color:['red', 'green','yellow','blueviolet','gray'],
            series : [
                {
                    name: '访问来源',
                    type: 'pie',
                    data:[
                        {value:335, name:'直接访问'},
                        {value:310, name:'邮件营销'},
                        {value:234, name:'联盟广告'},
                        {value:135, name:'视频广告'},
                        {value:1548, name:'搜索引擎'}
                    ]
                   
                }
            ]
        })
    }
  }
}
</script>



